import React, { useState, useEffect } from 'react';
import { API_URL } from '../../config';
import './CuttingEdge.css';
import { useNavigate } from 'react-router-dom';

function CuttingEdge() {
  const [articles, setArticles] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    fetchArticles();
  }, []);

  const fetchArticles = async () => {
    setIsLoading(true);
    try {
      const response = await fetch(`${API_URL}/api/cutting-edge`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      if (!Array.isArray(data) || data.length === 0) {
        throw new Error('');
      }
      setArticles(data);
      setError(null);
    } catch (error) {
      console.error(":", error);
      setError(error.message || ",");
      setArticles([]); // 
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading) return <div>...</div>;
  if (error) return <div>{error}</div>;

  return (
    <div className="cutting-edge">
      <div className="ce-header">
        <h1 className="ce-title"></h1>
        <div className="ce-translation-recommendation">
          <img src={require('../../assets/logos/ImmersiveTranslationLogo.png')} alt="" className="ce-translation-logo" />
          <div className="ce-translation-text">
            <p></p>
            <a href="https://immersivetranslate.com/" target="_blank" rel="noopener noreferrer" className="ce-translation-link">
              
            </a>
          </div>
        </div>
      </div>
      <button className="ce-back-button" onClick={() => navigate('/all-software')}></button>
      <div className="ce-grid">
        {articles.map((article, index) => (
          <div key={index} className="ce-article-item">
            <h2 className="ce-article-title">{article.title}</h2>
            <p className="ce-article-journal">: {article.journal}</p>
            <p className="ce-article-category">: {article.category || ''}</p>
            <p className="ce-article-summary">{article.summary}</p>
            <a href={article.url} target="_blank" rel="noopener noreferrer" className="ce-article-link"></a>
            <p className="ce-article-date">: {new Date(article.date).toLocaleDateString('zh-CN', { timeZone: 'Asia/Shanghai' })}</p>
          </div>
        ))}
      </div>
    </div>
  );
}

export default CuttingEdge;
